let app = {

   parallaxElement: null,
   parallaxElementPosition: 0,

   init() {
      this.parallaxElement = document.querySelector('.rossinante_banner')   
      if (this.parallaxElement) {
         this.parallaxElementPosition = window.getComputedStyle(this.parallaxElement).getPropertyValue('background-position-y');
           window.requestAnimationFrame(()=>{this.parallax()});
           window.addEventListener("scroll", (event) => {
              window.requestAnimationFrame(()=>{this.parallax()});
           })
           window.addEventListener("resize", (event) => {
            window.requestAnimationFrame(()=>{this.parallax()});
         }, false);
      }
   },

   parallax() {
      this.parallaxElement.style.backgroundPositionY = "calc(" + this.parallaxElementPosition + " + " + window.scrollY / 4 + "px)";
   },
}



setTimeout(() => {
   app.init()
}, 0)
