let app = {

   juliette: null,
   claire: null,

   init() {
      this.juliette = document.querySelector('.rossinante_communaute-grid > div div:nth-child(3)')
      this.claire = document.querySelector('.rossinante_communaute-grid > div div:nth-child(2)')
      if (this.juliette) {
         this.order()
         window.addEventListener("resize", (event) => {
            this.order()
         }, false);
      }
   },

   order() {
      this.claire.style.order = (this.juliette.getBoundingClientRect().top < 800) ? -1 : 0
   }
}

setTimeout(() => {
   app.init()
}, 0)
