import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

let app = {

   circles: [],

   init() {
      this.createCircleElement()
    /*   this.circlesAnimations()
      this.circles = document.querySelectorAll('.circle')
      if (this.circles.length) {
         this.circles.forEach((circle, i) => {
            let trigger = circle.querySelector('div')
            this.circlesAnimations(trigger, i + 1)
         })
      } */
   },

   createCircleElement() {
      let paragraphes = document.querySelectorAll('.rossinante_point_of_vue > *')

      paragraphes.forEach((paragraphe, i) => {
         let circleContainer = document.createElement("div")
         let circleDraw = document.createElement("div");
         circleContainer.classList.add("circle")
         circleDraw.classList.add("circle" + (i + 1))
         circleDraw.classList.add("circle" + (i + 1) + "-active")
         circleContainer.appendChild(circleDraw)
         paragraphe.after(circleContainer)
      })
   },

   circlesAnimations() {
      let circles = document.querySelectorAll('.circle')
      if (circles.length) {
         circles.forEach((circle, i) => {
            let trigger = circle.querySelector('div')
            ScrollTrigger.create({
               trigger: trigger,
               onEnter: ({ progress, direction, isActive }) => {
                  console.log(progress)
                  trigger.classList.add("circle" + (i + 1) + "-active")
               }
               //  markers:true
            });
         })
      }
   }
}



setTimeout(() => {
   app.init()
}, 0)
